// src/features/line-dashboard/api/history.js
"use server"
import { runQuery } from "@/lib/db"
import { LINE_SDWT_TABLE_NAME } from "./constants"
import { buildLineFilters } from "./lineFilters"
import { findColumn, pickBaseTimestampColumn } from "./columns"

const DIMENSION_CANDIDATES = [
  { key: "sdwt_prod", label: "설비 분임조" },
  { key: "user_sdwt_prod", label: "Eng 분임조" },
  { key: "eqp_id", label: "EQP" },
  { key: "main_step", label: "Main Step" },
  { key: "sample_type", label: "Sample Type" },
  { key: "line_id", label: "Line" },
]

const DEFAULT_WINDOW_DAYS = 14
const DEFAULT_DIMENSION_LIMIT = 5

function formatKstDate(date) {
  return new Intl.DateTimeFormat("en-CA", {
    timeZone: "Asia/Seoul",
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
  }).format(date)
}

function buildDateRange(days = DEFAULT_WINDOW_DAYS) {
  const today = new Date()
  const end = formatKstDate(today)
  const startDate = new Date(today)
  startDate.setDate(startDate.getDate() - Math.max(0, days - 1))
  const start = formatKstDate(startDate)
  return { from: start, to: end }
}

function enumerateDates(from, to) {
  const result = []
  const start = new Date(`${from}T00:00:00`)
  const end = new Date(`${to}T00:00:00`)
  if (!(start instanceof Date) || Number.isNaN(start.getTime())) return result
  if (!(end instanceof Date) || Number.isNaN(end.getTime())) return result
  for (let ts = start.getTime(); ts <= end.getTime(); ts += 24 * 60 * 60 * 1000) {
    const current = new Date(ts)
    result.push(current.toISOString().slice(0, 10))
  }
  return result
}

function normalizeBucketLabel(raw) {
  const value = typeof raw === "string" ? raw.trim() : null
  if (!value) return "미지정"
  return value
}

export async function fetchHistorySnapshot({
  lineId = null,
  days = DEFAULT_WINDOW_DAYS,
  dimensionLimit = DEFAULT_DIMENSION_LIMIT,
} = {}) {
  const columnRows = await runQuery(`SHOW COLUMNS FROM \`${LINE_SDWT_TABLE_NAME}\``)
  const columnNames = columnRows
    .map((column) => column?.Field)
    .filter((name) => typeof name === "string")

  if (!columnNames.length) {
    return {
      range: buildDateRange(days),
      totals: [],
      dimensions: [],
    }
  }

  const timestampColumn = pickBaseTimestampColumn(columnNames)
  if (!timestampColumn) {
    throw new Error("line_sdwt 테이블에서 사용할 수 있는 날짜 컬럼을 찾지 못했습니다.")
  }

  const sendJiraColumn = findColumn(columnNames, "send_jira")

  const window = buildDateRange(days)
  const { from, to } = window

  const { filters: lineFilters, params: lineParams, earlyEmpty } = await buildLineFilters(
    columnNames,
    lineId
  )

  if (earlyEmpty) {
    return {
      range: window,
      totals: enumerateDates(from, to).map((date) => ({ date, totalRows: 0, sendJira: 0 })),
      dimensions: [],
    }
  }

  const baseWhereParts = [...lineFilters]
  const baseParams = [...lineParams]

  if (from) {
    baseWhereParts.push(`\`${timestampColumn}\` >= ?`)
    baseParams.push(`${from} 00:00:00`)
  }

  if (to) {
    baseWhereParts.push(`\`${timestampColumn}\` <= ?`)
    baseParams.push(`${to} 23:59:59`)
  }

  const totalsWhere = baseWhereParts.length ? `WHERE ${baseWhereParts.join(" AND ")}` : ""

  const totalsQuery = `
    SELECT
      DATE_FORMAT(\`${timestampColumn}\`, '%Y-%m-%d') AS day,
      COUNT(*) AS total_rows
      ${
        sendJiraColumn
          ? `, SUM(CASE WHEN \`${sendJiraColumn}\` = 1 THEN 1 ELSE 0 END) AS send_jira`
          : ", 0 AS send_jira"
      }
    FROM \`${LINE_SDWT_TABLE_NAME}\`
    ${totalsWhere}
    GROUP BY day
    ORDER BY day
  `

  const totalsRows = await runQuery(totalsQuery, baseParams)
  const daysList = enumerateDates(from, to)

  const totalsByDay = new Map(
    totalsRows.map((row) => [row?.day, { totalRows: Number(row?.total_rows) || 0, sendJira: Number(row?.send_jira) || 0 }])
  )

  const totals = daysList.map((day) => {
    const entry = totalsByDay.get(day)
    if (entry) {
      return { date: day, totalRows: entry.totalRows, sendJira: entry.sendJira }
    }
    return { date: day, totalRows: 0, sendJira: 0 }
  })

  const dimensions = []

  for (const candidate of DIMENSION_CANDIDATES) {
    const columnName = findColumn(columnNames, candidate.key)
    if (!columnName) continue

    const bucketExpr = `COALESCE(NULLIF(TRIM(\`${columnName}\`), ''), '__UNKNOWN__')`
    const summaryQuery = `
      SELECT ${bucketExpr} AS bucket, COUNT(*) AS total
      FROM \`${LINE_SDWT_TABLE_NAME}\`
      ${totalsWhere}
      GROUP BY bucket
      ORDER BY total DESC
      LIMIT ?
    `

    const summaryRows = await runQuery(summaryQuery, [...baseParams, dimensionLimit])
    if (!summaryRows.length) continue

    const buckets = summaryRows
      .map((row) => (typeof row?.bucket === "string" ? row.bucket : "__UNKNOWN__"))
      .filter((value, index, self) => value && self.indexOf(value) === index)

    if (!buckets.length) continue

    const placeholders = buckets.map(() => "?").join(", ")
    const detailWhereParts = [...baseWhereParts, `${bucketExpr} IN (${placeholders})`]
    const detailParams = [...baseParams, ...buckets]
    const detailWhere = detailWhereParts.length ? `WHERE ${detailWhereParts.join(" AND ")}` : ""

    const detailQuery = `
      SELECT
        DATE_FORMAT(\`${timestampColumn}\`, '%Y-%m-%d') AS day,
        ${bucketExpr} AS bucket,
        COUNT(*) AS total
      FROM \`${LINE_SDWT_TABLE_NAME}\`
      ${detailWhere}
      GROUP BY day, bucket
      ORDER BY day, bucket
    `

    const detailRows = await runQuery(detailQuery, detailParams)

    const seriesMap = new Map()
    for (const bucket of buckets) {
      seriesMap.set(bucket, new Map())
    }

    for (const row of detailRows) {
      const bucket = typeof row?.bucket === "string" ? row.bucket : "__UNKNOWN__"
      if (!seriesMap.has(bucket)) continue
      const day = row?.day
      const count = Number(row?.total) || 0
      if (typeof day === "string" && day.length === 10) {
        seriesMap.get(bucket).set(day, count)
      }
    }

    const series = buckets.map((bucket, index) => {
      const label = bucket === "__UNKNOWN__" ? "미지정" : normalizeBucketLabel(bucket)
      const dayMap = seriesMap.get(bucket) ?? new Map()
      return {
        id: `${candidate.key}-${index}`,
        bucket,
        label,
        colorIndex: index,
        points: daysList.map((day) => ({ date: day, value: dayMap.get(day) ?? 0 })),
      }
    })

    dimensions.push({
      key: candidate.key,
      label: candidate.label,
      series,
    })
  }

  return {
    range: window,
    totals,
    dimensions,
  }
}
